  height: 100vh;
  background-color: var(--accent-color);
  transform-origin: bottom;
  transition: transform 1000ms var(--cubic);
  margin-bottom: auto;
  padding-top: 200px;
  padding-left: 73px;
  padding-right: 74px;
  position: fixed;
  top: 0;
  right: 0;
  transform: scaleY(0);
}

.mobile-container.is-open {
  transform-origin: top;
  transition: transform 1000ms var(--cubic);
  transform: scaleY(1);
}

